import React from 'react'
import { graphql, Link } from 'gatsby'

import Container from '../components/container'
import Head from '../components/head'
import Img from 'gatsby-image'

import '../styles/404.css'


const NotFound = ({ data }) => {
    return (
        <Container>
            <Head title="404"/>
            <div className="row">

                <div className="imgCont">
                    {!!data.nfImage && !!data.nfImage.childImageSharp
                        ? <Img fluid={data.nfImage.childImageSharp.fluid}
                        alt={"Error 404: Page Not Found"} />
                        : <img src={data.nfImage.publicURL} alt={"Error 404: Page Not Found"} />
                    }
                </div>

                <div className="textCont">
                    <p>404</p>
                    
                    <h3>
                        Well, it looks like you<br/>got out of your way?
                    </h3>

                    <h4>
                        So do I, helping you<br/>brand, build &amp; grow.
                    </h4>

                    <div className="cta_container">
                        <button className="priButton">
                            <Link to="/about" className="ctaPri">HIRE ME</Link>
                        </button>
                        <button className="secButton">
                            <Link to="/" className="ctaSec">BACK HOME</Link>
                        </button>
                    </div>
                </div>

            </div>
        </Container>
    )
}

export default NotFound


export const nfQuery = graphql`
    query {
        nfImage: file (relativePath: {eq: "not_found.png" }) {
            childImageSharp {
                fluid(maxWidth: 2048) {
                    ...GatsbyImageSharpFluid
                }
            }
            publicURL
        }
    }
`